h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
}

.is-hidden {
  display: none;
}

@media only screen and (max-width: 1199px) {
  .desk-only {
    display: none;
