.screen {
  height: 100vh;
  background: #131313;
}

.screen__slider {
  display: none;
  height: 100%;
  animation: fadeIn 1.3s;
  overflow: hidden;
}

.screen__slider.slick-initialized {
  display: block;
}

.screen__slider-item {
  position: relative;
  height: 100vh;
}

.screen__slider-img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  transform: scale(1.2);
  transition: all 1s cubic-bezier(0.7, 0, 0.3, 1);
  opacity: .2;
}

.is-ie .screen__slider-img {
  transform: scale(1);
}

.slick-active .screen__slider-img {
  transform: scale(1);
  opacity: 1;
}

.screen__slider-container {
  display: flex;
  align-items: center;
  height: 100%;
}

.screen__slider-inner {
  position: relative;
  z-index: 1;
  width: 100%;
  max-width: 780px;
  padding-top: 135px;
}

.screen__slider-title {
  margin-bottom: 40px;
  font-size: 46px;
  color: #fff;
}

@media only screen and (max-width: 767px) {
  .screen__slider-title {
    font-size: 30px;
    line-height: 36px;
  }
}

.screen__slider-dots {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.screen__slider-dots li {
  margin: 0 5px;
}

.screen__slider-dots li.slick-active button {
  background: #fff;
}

.screen__slider-dots li button {
  width: 40px;
  height: 4px;
  background: rgba(255, 255, 255, 0.3);
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  transition: .4s ease;
}

